.product-card{
    width: 30%;
    padding: 10px;
    box-shadow: 4px 4px 4px rgba(0,0,0,.2);
    border-radius: 10px;
    cursor: pointer;
    transition: 0.5s box-shadow ease-in-out;
    margin-bottom: 20px;
}
.product-card:hover{
    box-shadow: 8px 8px 8px rgba(0,0,0,.5);
    transition: 0.5s box-shadow ease-in-out;
}
.product-card .product-image{
    padding: 10px;
}
.product-card .product-image img{
    width: 60%;
    margin: auto;
    height: 200px;
    transition: 0.5s transform ease-in-out;
}
.product-card .product-image img:hover{
   transform: scale(0.8);
   transition: 0.5s transform ease-in-out;
}
.product-info{
    padding: 10px;
}
.products-buttons button:nth-child(1) a:hover{
  background-color: var(--color-secondery);
  color: var(--color-light);
  transition: 0.5s all ease-in-out;
}
.products-buttons button:nth-child(2) a:hover{
    background-color: var(--color-light);
    color: var(--color-secondery);
    transition: 0.5s all ease-in-out;
  }

  @media(max-width:1170px){
    .product-card{
        width: 40%;
        margin: auto;
        margin-bottom: 20px;
    }
  }
  @media(max-width:991px){
    .product-card{
        width: 40%;
        margin: auto;
        margin-bottom: 20px;
    }
  }
  @media(max-width:877px){
    .product-card{
        width: 80%;
        margin: auto;
        margin-bottom: 20px;
    }
  }
  @media(max-width:450px){
    .product-card{
        width: 100%;
        margin: auto;
        margin-bottom: 20px;
    }
    .products-buttons button a{
        font-size: 11px;
    }
  }
  @media(max-width:678px){
    .product-card .product-image img{
        width: 60%;
        margin: auto;
        height: 150px;
        transition: 0.5s transform ease-in-out;
    }
  }
  