
.product-sekelton-image,.product-sekelton-Header,.product-sekelton-category,.product-sekelton-paragraph{
    animation-name: sekelton;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
}
.text{
    font-size: 30px;
    line-height: 36px;
    font-weight: 700;
}
@keyframes sekelton {
    0%{
        background-color: rgb(236, 236, 236);
    }
    50%{
        background-color: rgb(179, 179, 179);
    }
    100%{
        background-color: rgb(236, 236, 236);
    }
}
@media(max-width:1023px){
    .product-image{
        padding: 20px;
        margin: auto;
        width: 40%;
    }
    .product-information{
        width: 60%;
    }
    .product-image img{
        width: 100%;
        height:300px
    }
}
@media(max-width:787px){
    .product-image{
        padding: 20px;
        margin: auto;
        width: 100%;
        padding-left: 0px;
        padding-right: 0px;
    }
    .product-information{
        width: 70%;
        margin: auto;
        
    }
    .product-image img{
        width: 70%;
        height:300px
    }
}
@media(max-width:668px){
    .text{
        font-size: 24px !important;
        
    }
    .product-price-rate span{
        font-size: 13px;
    }
   
}
@media(max-width:787px){
    .product-image{
        padding: 20px;
        margin: auto;
        width: 100%;
        padding-left: 0px;
        padding-right: 0px;
    }
    .product-information{
        width: 90%;
        margin: auto;
        
    }
    .product-image img{
        width: 90%;
        margin: auto;
        height:300px
    }
}