.input-groups{
    display: block;
    width: 90%;
    margin: auto;
    height: 40px;
    padding-left: 10px;
    border:1px solid var(--color-text);
    border-radius: 10px;
    outline:none;
    margin-bottom: 20px;
    transition: 0.5s all ease-in-out;
}
.input-groups:focus{
    border-radius: 10px;
    outline:1px solid var(--color-text);
    transition: 0.5s all ease-in-out;
}
.label{
    position: relative;
    padding-left:10px ;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif
}
.label::before{
 content: '*';
 top: 0px;
 left: 10px;
 color: red;
 
}
.tech-support{
    border-radius: 20px;
    box-shadow: 0px 0px 8px rgba(0,0,0,.3);
    margin-bottom: 50px;
    padding:30px 0px;
}
.input-submit{
    display: block;
    margin: auto;
    background-color: var(--color-secondery);
    padding: 10px 20px;
    border-radius: 10px;
    cursor: pointer;
    color: white;
}
@media(max-width:1023px){
    .support-image{
        display: none;
    }
    .form-group{
        width: 80%;
        margin: auto;
    }
}