.navbar-item {
    font-size: 18px;
    font-weight: normal;
    color: var(--color-light);
    transition: 0.5s color ease-in-out;
    letter-spacing: 1px;
  }
  .underline-item{
    width: 0%;
    height: 2px;
    background-color: var(--color-secondery);
    transition: 0.5s width ease-in-out;
  }
  .navbar-item:hover>.underline-item{
    width: 100%;
  }
  .brand-logo{
    font-family:fantasy, sans-serif;
  }
  .toggle-btn{
  
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    padding: 3px 0px;
  }
  .toggle-btn span{
    background-color: white;
    width: 30px;
    height: 3px;
    margin-bottom: 8px;
  }
  .pop-up-log-out{
    background-color: white !important;
    opacity: 1 !important;
    border-radius: 20px;
}
  @media(min-width:992px){
    .toggle-btn{
        display: none;
    }
    .navbar-items-group{
      flex-direction: row !important;
    }
}
@media(max-width:991px){
  .navbar-items-group{
    flex-direction: column !important;
  }
  .navbar-items{
    position: absolute;
    right: 2px;
    width: 50%;
    background-color: white !important;
    box-shadow: 2px 8px 8px rgba(0,0,0,.2);
    padding: 20px;
    height: 60vh;
    z-index: 1 !important;
  }
  .navbar-item{
    color: var(--color-secondery);
    padding: 5px;
  }
}
@media(max-width:768px){
  .navbar-items-group{
    flex-direction: column !important;
  }
}