.hero button{
    text-align: center;
    display: block;
    transition: 0.5s background-color ease-in-out;
}
.hero button:hover{

    background-color: var(--color-light);
    transition: 0.5s background-color ease-in-out;
}
.hero h1{
    font-size: 35px;
}
.hero-overly{
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: rgba(0,0,0,.6);
}
@media(max-width:991px){
    .hero h1{
        font-size: 25px;
        width: 70%;
    }
    .hero p{
        width: 60%;
        margin-bottom: 20px;
        margin-top: 20px;
    }
}
@media(max-width:678px){
    .hero h1{
        font-size: 25px;
        width: 90%;
    }
    .hero p{
        width: 90%;
        margin-bottom: 20px;
        margin-top: 20px;
    }
}