.filter-btn{
    border: 1px solid var(--color-secondery);
  }
  .filter-btn:hover{
    background-color: white;
    color: var(--color-secondery);
    border: 1px solid var(--color-secondery);
    transition: 0.5s all ease-in-out;
  }
  .filter-by-price select{
    border: none;
    outline: none;
    cursor: pointer;
  }
  .filter-by-price select:focus{
    border: none;
    outline: none;
  }

  .sekelton{
    width: 80%;
    display: flex;
    margin: auto;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}
.sekelton-shap{
    width: 27%;
    height: 200px;
    background-color: rgb(199, 199, 199);
    animation: backgroundChange;
    animation-duration: 2s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    transition: 0.5s all ease-in-out;
    margin-bottom: 20px;
}

@keyframes backgroundChange{
    0%{
        background-color: rgb(199, 199, 199);
    }
    50%{
        background-color: rgb(255,255,255);
    }
    100%{
        background-color: rgb(199, 199, 199);
    }
}
@media(max-width:1170px){
  .sekelton-shap{
      width: 40%;
      margin: auto;
      margin-bottom: 20px;
  }
}
@media(max-width:991px){
  .sekelton-shap{
      width: 40%;
      margin: auto;
      margin-bottom: 20px;
  }
}
@media(max-width:877px){
  .sekelton-shap{
      width: 80%;
      margin: auto;
      margin-bottom: 20px;
  }
}
@media(max-width:680px){
  .filter{
     width: 100% !important;
  }
  .filter-btn{
      width: 100%;
      margin-bottom: 10px;
  }
}