.section-title{
 width: 50%;
 margin: auto;
 display: flex;
 flex-direction: row;
 justify-content: space-between;
 align-items: center;
}
.section-title .title{
    background-color: transparent;
    font-size: 30px;
    font-weight: bold;
    color: var(--color-primary);
}
.section-title span{
    background-color: var(--color-secondery);
}
.section-title .first{
    width: 30px;
    height: 3px;
}
@media(max-width:991px){
    .section-title{
        width: 70%;
    }
}
@media(max-width:678px){
    .section-title .title{
        font-size: 19px;
    }
    .section-title{
        width: 80%;
    }
    .section-title .first{
        width: 25px;
    }
    .section-title .first:nth-child(1){
        display: none;
    }
    .section-title .first:nth-child(7){
        display: none;
    }
}

