.login-input-item input{
    border: none;
    border-radius: 0px;
    border-bottom: 1px solid red;
    outline: 0px;
    transition: 0.5s all ease-in-out;
}
.login-input-item input:focus{
    border-radius: 10px;
    outline:none;
    border: 1px solid grey;
    transition: 0.5s all ease-in-out;
}
.socialMedia-login button a{
    border: 1px solid red;
    border-radius: 10px;
}